.custom-button {
  position: absolute;
  background-color: transparent;
  border-radius: 50%;
  border: 2px solid white;
  width: 3rem;
  height: 3rem;
  top: 50%;
  cursor: pointer; }
  .custom-button:hover {
    background-color: lightgrey; }

.right {
  right: 1rem; }

.left {
  left: 1rem; }

.indicator-container {
  position: fixed;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  bottom: 1rem;
  display: flex;
  display: -webkit-flex;
  flex-direction: row; }
  .indicator-container button {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 2px solid white;
    cursor: pointer;
    margin-left: .25rem;
    margin-right: .25rem; }
    .indicator-container button:hover {
      background-color: lightgrey; }

.button-selected {
  background-color: lightgrey; }

.button-unselected {
  background-color: transparent; }

.container {
  position: absolute;
  width: 100%;
  height: 100%; }

.slide-container {
  position: absolute;
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  color: white;
  font-size: 2rem;
  background-color: rgba(140, 140, 140, 0.4); }
  .slide-container .intro-title {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    font-size: 4rem !important; }
    .slide-container .intro-title div:nth-child(2) {
      cursor: pointer; }
      .slide-container .intro-title div:nth-child(2):hover {
        color: lightgrey !important; }
  .slide-container .body {
    width: 60%; }
  .slide-container .col-null-center {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center; }
  .slide-container .col-flex-s-center {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: flex-start; }
  .slide-container .col-sb-center {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center; }
  .slide-container .row-sb-center {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
  .slide-container .row-sa-center {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center; }
  .slide-container .w-40 {
    width: 40%; }
  .slide-container .w-50 {
    width: 50%; }
  .slide-container .w-60 {
    width: 60%; }
  .slide-container .w-70 {
    width: 70%; }
  .slide-container .w-80 {
    width: 80%; }
  .slide-container .w-90 {
    width: 90%; }
  .slide-container .w-100 {
    width: 100%; }
  .slide-container .font-1 {
    font-size: 1rem !important; }
  .slide-container .font-1-5 {
    font-size: 1.5rem !important; }

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
